'use client';

import CountUp from 'react-countup';

const stats = [
  {
    value: 20,
    label: 'Invoices Processed Per Year',
  },
  {
    value: 100,
    label: 'Transactions Processed Per Year',
  },
  {
    value: 50,
    label: 'Runs On Marg ERP Software',
  },
  {
    value: 1,
    label: 'Businesses Served Worldwide',
  },
  {
    value: 850,
    label: 'Sales & Support Centers',
  },
  {
    value: 8500,
    label: 'Sales & Service Professionals',
  },
];

export default function StatsSection() {
  return (
    <section className="py-2  ">
      <h2 className="text-2xl md:text-4xl font-bold text-center mb-12 text-white bg-orange-600  py-2 mx-4 rounded-full">
        Empowering Businesses
      </h2>

      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-6 gap-6 max-w-6xl mx-auto rounded-2xl p-8 px-30">
        {stats.map((item, index) => (
          <div
            key={index}
            className="bg-white dark:bg-gray-700 rounded-xl border shadow hover:shadow-lg transition p-6 text-center flex flex-col items-center"
          >
            <h3 className="text-2xl font-bold mt-4 text-black dark:text-gray-100">
              <CountUp end={item.value} duration={2} />
            </h3>
            <p className="text-sm text-black dark:text-gray-400 mt-1">{item.label}</p>
          </div>
        ))}
      </div>
    </section>
  );
}
